@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

:root{
    --purple-color: #8d227a;
    --pink-color: #d9176d;
    --grey-color: #f3f3f3;
    --white-color: #fff;
    --black-color: #010101;
    --light-black-color: #717171;
    --transition: all 300ms ease-in-out;
}

*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

html{
    font-size: 10px;
    color: var(--white-color);
    background-color: var(--black-color);
}

body{
    font-size: 1.6rem;
    line-height: 1.7;
    font-family: 'Poppins', sans-serifr;
    color: var(--white-color);
    background-color: var(--black-color);
}

ul{
    list-style-type: none;
}

a{
    text-decoration: none;
    color: var(--white-color);
}

button{
    outline: none;
    border: none;
    font-family: inherit;
    background-color: transparent;
    color: white;
    cursor: pointer;
}

img{
    width: 100%;
    display: block;
}

svg{
   color: white;
}

input{
    border: none;
    outline: none;
    font-family: inherit;
    font-size: 1.6rem;
}

.resize-animation-stopper *{
    animation: none!important;
    transition: none!important;
}

.bg-pink{background-color: var(--pink-color);}
.bg-purple{background-color: var(--purple-color);}
.bg-white{background-color: var(--white-color);}


.text-black{color: var(--black-color);}
.text-white{color: var(--white-color);}
.text-purple{color:var(--purple-color);}
.text-light-black{color:var(--light-black-color);}
.text-uppercase{text-align: center;}
.text-center{text-align: center;}
.text-capitalize{text-transform: capitalize;}
.text-italic{font-style: italic;}

.fw-3{font-weight: 300;}
.fw-4{font-weight: 400;}
.fw-5{font-weight: 500;}
.fw-6{font-weight: 600;}
.fw-7{font-weight: 700;}
.fw-8{font-weight: 800;}
.fw-9{font-weight: 900;}

.fs-15{font-size: 1.5rem;}
.fs-16{font-size: 1.6rem;}
.fs-17{font-size: 1.7rem;}
.fs-18{font-size: 1.8rem;}
.fs-20{font-size: 2rem;}
.fs-22{font-size: 2.2rem;}
.fs-24{font-size: 2.4rem;}
.fs-26{font-size: 2.6rem;}

.ls-1{letter-spacing: 1px;}
.ls-2{letter-spacing: 2px;}

.flex{
    display: flex;
    align-items: center;
}

.flex-column{
    flex-direction: column;
}

.flex-sb{
    justify-content: space-between;
}

.flex-c{
    justify-content: center;
}

.grid{
    display: grid;
}

.container{
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 2rem;
}

.section-title{
    font-weight: 600;
    text-transform: uppercase;
    padding: 18px 0 24px 0;
}